
import { defineComponent } from "vue";
import {
  loadChecks,
  saveChecks,
  Checks,
  saveSumChecks,
  loadSumChecks,
} from "../../API";

export default defineComponent({
  mounted() {
    this.checks = loadChecks();
    this.checksSum = loadSumChecks();
  },
  data() {
    return {
      checksSum: 0,
      payed: false,
      inputWhat: "",
      inputWhen: "",
      inputHowMuch: 0,
      checks: [] as Checks[],
    };
  },
  methods: {
    deleteCheck(check: any) {
      this.checks.splice(this.checks.indexOf(check), 1);
      console.log("Check erfolgreich gelöscht");
      this.calcSum();
      saveChecks(this.checks);
    },
    addCheck() {
      this.checks.push({
        what: this.inputWhat,
        howMuch: this.inputHowMuch,
        when: this.inputWhen,
        payed: this.payed,
      });
      this.inputWhat = "";
      this.inputHowMuch = 0;
      this.inputWhen = "";

      saveChecks(this.checks);
      this.calcSum();

      console.log("Check erfolgreich erstellt");
    },
    savePayed() {
      this.calcSum();
      saveChecks(this.checks);
    },
    calcSum() {
      this.checksSum = 0;
      for (let sum of this.checks) {
        this.checksSum += sum.howMuch;
      }
      console.log(this.checksSum);
      saveSumChecks(this.checksSum);
    },
  },
});
