
import { defineComponent } from "vue";
import { Options } from "vue-class-component";
import {
  loadIncome,
  saveIncome,
  loadOutcome,
  saveOutcome,
  loadChecks,
  saveChecks,
  saveSumChecks,
  loadSumChecks,
  incomeList,
  outcomeList,
  Checks,
} from "../../API";

export default defineComponent({
  mounted() {
    this.checks = loadChecks();
    this.incomeList = loadIncome();
    this.outcomeList = loadOutcome();
    this.checksSum = loadSumChecks();
    this.calcIncome();
    this.calcOutcome();
    this.calcBalance();
  },
  data() {
    return {
      incomeSum: 0,
      outcomeSum: 0,
      balance: 0,
      checksSum: 0,
      incomeWhat: "",
      incomeHowMuch: 0,
      outcomeWhat: "",
      outcomeHowMuch: 0,
      incomeList: [] as incomeList[],
      outcomeList: [] as outcomeList[],
      checks: [] as Checks[],
    };
  },
  methods: {
    addIncome() {
      this.incomeList.push({
        incomeWhat: this.incomeWhat,
        incomeHowMuch: this.incomeHowMuch,
      });
      this.incomeWhat = "";
      this.incomeHowMuch = 0;

      saveIncome(this.incomeList);
      this.calcIncome();
      this.calcBalance();

      console.log("Einkommen erfolgreich erstellt");
    },
    deleteIncome(income: any) {
      this.incomeList.splice(this.incomeList.indexOf(income), 1);
      saveIncome(this.incomeList);
      this.calcIncome();
      this.calcBalance();
      console.log("Einkommen erfolgreich gelöscht");
    },
    addOutcome() {
      this.outcomeList.push({
        outcomeWhat: this.outcomeWhat,
        outcomeHowMuch: this.outcomeHowMuch,
      });
      this.outcomeHowMuch = 0;
      this.outcomeWhat = "";

      saveOutcome(this.outcomeList);
      this.calcOutcome();
      this.calcBalance();

      console.log("Ausgaben erfolgreich erstellt");
    },
    deleteOutcome(outcome: any) {
      this.outcomeList.splice(this.outcomeList.indexOf(outcome), 1);
      saveOutcome(this.outcomeList);
      this.calcOutcome();
      this.calcBalance();
      console.log("Ausgaben erfolgreich gelöscht");
    },
    deleteCheck(check: any) {
      this.checks.splice(this.checks.indexOf(check), 1);
      console.log("Check erfolgreich gelöscht");
      this.calcSum();
      saveChecks(this.checks);
    },
    calcSum() {
      this.checksSum = 0;
      for (let sum of this.checks) {
        this.checksSum += sum.howMuch;
      }
      console.log(this.checksSum);
      saveSumChecks(this.checksSum);
    },
    calcIncome() {
      this.incomeSum = 0;
      for (let sum of this.incomeList) {
        this.incomeSum += sum.incomeHowMuch;
      }
      console.log(this.incomeSum);
    },
    calcOutcome() {
      this.outcomeSum = 0;
      for (let sum of this.outcomeList) {
        this.outcomeSum += sum.outcomeHowMuch;
      }
      console.log(this.outcomeSum);
    },
    calcBalance() {
      console.log(this.incomeSum, this.outcomeSum, this.checksSum);
      this.balance = this.incomeSum - this.outcomeSum - this.checksSum;
      console.log("test");
    },
  },
});
